import { generatePercentagePrompt } from "@rp/react/common/watchlists"
import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function ytd(tableName: string) {
  const title = "YTD Chg %"
  return genericColumn({
    endpoint: "companyPriceChanges",
    columnKeys: ["ytd"],
    size: 90,
    title,
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          queryResult={info.row.original.financialData.companyPriceChanges}
          tooltip={"Explain the price change"}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              ...generatePercentagePrompt(
                info.row.original.entityInfo.name,
                "was",
                info.row.original.financialData.companyPriceChanges?.data?.["ytd"] ?? 0,
                "since the beginning of the year",
              ),
              value: info.row.original.financialData.companyPriceChanges?.data?.["ytd"],
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyPriceChanges?.data?.["ytd"]}
        />
      )
    },
  })
}

export { ytd }
